@font-face {
  font-family: "RubikBurned";
  src: url("../fonts/RubikBurned-Regular.ttf") format("truetype");
}

@font-face {
  font-family: "Tiny5";
  src: url("../fonts/Tiny5-Regular.ttf") format("truetype");
}

@font-face {
  font-family: "Jersey20";
  src: url("../fonts/Jersey20-Regular.ttf") format("truetype");
}

@font-face {
  font-family: "FreckleFace";
  src: url("../fonts/FreckleFace-Regular.ttf") format("truetype");
}

@font-face {
  font-family: "VT323";
  src: url("../fonts/VT323-Regular.ttf") format("truetype");
}

#popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: lightblue;
  z-index: 10;
}

#popup {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: lightblue;
  padding: 20px;
  border-radius: 8px;
  z-index: 1000;
}

#close-popup {
  background: #ff4d4f;
  color: white;
  border: none;
  padding: 5px;
  cursor: pointer;
  border-radius: 4px;
}

#login-form {
  display: flex;
  flex-direction: column;
}

#login-form input {
  margin: 5px 0;
  padding: 8px;
  border-radius: 4px;
  border: 1px solid #ddd;
}

#login-form button {
  padding: 10px;
  background: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

/* Ensure the Login button is visible */
#login-button {
  padding: 10px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

/* Add blur effect */
.blur {
  filter: blur(5px);
}

body {
  font-family: Arial, sans-serif;
  display: flex;
  height: 100vh;
  margin: 0;
  position: relative;
}

#app {
  flex-grow: 1;
  display: flex;
}

#header-bar {
  font-family: "RubikBurned", sans-serif;
  font-size: 2em;
  position: fixed;
  background-color: #776483;
  color: #fff;
  padding: 24px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: calc(100% - 40px);
}

.header-container h1 {
  margin: 0;
}

/* Sidebar navigation */
#sidebar {
  background-color: #c6754d;
  color: white;
  width: 220px;
  padding: 20px;
  box-sizing: border-box;
  margin-top: 105px;
  position: fixed;
  height: calc(100vh - 105px);
}

#sidebar h2 {
  background-color: #c6754d;
  font-family: "FreckleFace", sans-serif;
  font-size: 2em;
  letter-spacing: 0.2rem;
  margin-top: -3px;
  margin-bottom: 1em;
}

#sidebar ul {
  list-style: none;
  padding: 0;
}

#sidebar ul li {
  font-family: "Jersey20", sans-serif;
  font-size: 1.55em;
  margin-bottom: 20px;
}

#sidebar ul li a {
  color: white;
  text-decoration: none;
  font-weight: bold;
}

#sidebar ul li a:hover {
  text-decoration: underline;
}

/* Main content area */
#main-content {
  flex-grow: 1;
  background-color: #f5f5f5;
  display: flex;
  flex-direction: column;
  margin-top: 105px;
  margin-left: 225px;
}

#header {
  font-family: "Jersey20", sans-serif;
  background-color: #44426e;
  color: white;
  padding: 15px;
  font-size: 2em;
  height: 200fpx; /* Fixed height */
  width: 100%; /* Full width */
  box-sizing: border-box; /* Ensures padding is included in height */
}

#chat-container {
  flex-grow: 1;
  padding: 20px;
  overflow-y: auto;
}

.message {
  font-family: "VT323", sans-serif;
  font-size: 1.4em;
  background-color: #ffffff;
  border: 1px solid #44426e;
  padding: 10px;
  margin-bottom: 18px;
  border-radius: 5px;
  max-width: 70%;
  position: relative;
}

.message.user1:after {
  content: "";
  position: absolute;
  bottom: -10px;
  left: 20px;
  width: 0;
  height: 0;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-top: 10px solid #44426e;
}

.message.user2:after {
  content: "";
  position: absolute;
  bottom: -10px;
  right: 20px;
  width: 0;
  height: 0;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-top: 10px solid #44426e;
}

/* Chat input area */
#chat-input-container {
  display: flex;
  align-items: center;
  padding: 20px;
  background-color: #e0e0e0;
}

#chat-input-container input {
  flex-grow: 1;
  padding: 10px;
  font-size: 1em;
  resize: none;
  height: 60px;
  border-radius: 5px;
  border: 1px solid #ccc;
}

#send-button {
  background-color: #3c1634;
  color: #3c1634;
  border: none;
  padding: 10px 20px;
  margin-left: 10px;
  cursor: pointer;
  border-radius: 5px;
}

#send-button:hover {
  background-color: #292643;
}

